import { login, getCurrUser, logout } from '@/api/user.js'
import router from '../../router'
import Message from '../../components/message'

const user = {
  state: {
    currUser: null,
    isLogin: 0,
    currHotel: {}
  },
  // getters 相当于计算属性
  getters: {},
  // 更改状态 同步
  mutations: {
    login (state, currUser) {
      state.isLogin += 1
      state.currUser = currUser
      state.currHotel = currUser.currHotel
    },
    logout (state) {
      state.isLogin = 0
      state.currUser = {}
      state.currHotel = {}
    },
    setIsLogin (state) {
      state.isLogin = false
    }
  },
  // 可以异步
  actions: {
    login (context, loginInfo) {
      login(loginInfo).then(res => {
        if (res.data.code === 0) {
          let currUser = res.data.data
          context.commit('login', currUser)
          Message.success('登录成功')
          router.push('/report/overview')
        } else {
          Message.error(res.data.code + "：" + res.data.msg);
          console.log(res.data.code + '：' + res.data.msg)
        }
      })
    },
    logout (context) {
      logout().then(res => {
        if (res.data.code === 0) {
          context.commit('logout')
          Message.success('退出系统成功')
          router.push('/login')
        } else {
          console.log(res.data.code + '：' + res.data.msg)
        }
      })
    },
    currUser (context) {
      getCurrUser()
        .then(res => {
          if (res.data.code === 0) {
            let currUser = res.data.data
            context.commit('login', currUser)
          } else {
            console.log(res.data.code + '：' + res.data.msg)
          }
        })
    }
  }
}

export default user
