<template>
  <div>
    <v-dialog
      v-model="dialog"
      persistent
      max-width="600px"
    >
      <v-card>
        <v-card-title>
          <span class="">{{ title }} -- 当前现金：{{ onDutyInfo?onDutyInfo.currentCash : '0'}} 元
          </span>
        </v-card-title>
        <v-card-text>
          <v-form ref="collectForm">
            <v-text-field
              :rules="[(v => !!v || '收账金额不能为空'),(v => v > 0 || '收账金额不能小于0')]"
              :label="cashLabel"
              type="number"
              v-model="collect.flowMoney"
            ></v-text-field>
            <v-text-field
              :rules="[v => !!v || '密码不能为空']"
              label="密码"
              type="password"
              v-model="collect.userPwd"
            ></v-text-field>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="blue darken-1"
            flat
            @click="dialog = false"
          >取消</v-btn>
          <v-btn
            :loading="loading"
            :disabled="loading"
            color="blue darken-1"
            flat
            @click="submit"
          >确认</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import {collectCash, addCash} from '../../api/finance.js'
import { getOnDuty, handover } from "@/api/handover.js";
export default {
  name: "Collect",
  props: {
    show: Boolean,
    cashType: String
  },
  data() {
    return {
      dialog: false,
      loading: false,
      title: '财务收账',
      cashLabel: '收账金额',
      collect: {
          flowMoney: '',
          userPwd: ''
      },
      onDutyInfo: null
    };
  },
  watch: {
    show(show) {
      this.dialog = show;
    },
    dialog(dialog) {
      if (dialog) {
          if (this.cashType === 'out') {
              this.title = '财务收账';
              this.cashLabel = '收账金额';
          } else if (this.cashType === 'in') {
              this.title = '添加备用金';
              this.cashLabel = '备用金金额';
          }
          this.getOnDutyInfo();
      } else {
        this.$emit("collect-close");
        this.$refs.collectForm.reset();
      }
    }
  },
  methods: {
      submit() {
      if (this.$refs.collectForm.validate()) {
         this.loading = true;
          if (this.cashType === 'out') {
              collectCash(this.collect).then(res => {
                if (res.data.code === 0) {
                    this.$Msg.success("收账成功");
                    this.dialog = false;
                } else {
                    this.$Msg.error(res.data.code + "：" + res.data.msg);
                }
                this.loading = false;
            });
          }
          if (this.cashType === 'in') {
              addCash(this.collect).then(res => {
                if (res.data.code === 0) {
                    this.$Msg.success("添加备用金成功");
                    this.dialog = false;
                } else {
                    this.$Msg.error(res.data.code + "：" + res.data.msg);
                }
                this.loading = false;
            });
          }
      }
    },
    getOnDutyInfo () {
        getOnDuty().then(res => {
            if (res.data.code === 0) {
                this.onDutyInfo = res.data.data;
            } else {
                this.$Msg.error(res.data.code + "：" + res.data.msg);
            }
        });
    },
  },
};
</script>

