import Vue from 'vue'
import Router from 'vue-router'
import routes from './routers'
import store from '../store'

Vue.use(Router)

const router = new Router({
  routes
})

router.beforeEach((to, from, next) => {
  if (to.meta.auth && to.meta.auth !== false && !store.state.user.currUser) {
    store.dispatch('currUser')
    next()
  } else {
    next()
  }
  // store.commit('currTitle', to.name)
})
router.afterEach((to, from) => {
  store.commit('setCurrTitle', to.name || null)
})

export default router
