import store from '../../store'

const Message = {
  open (type, options) {
    options.snackbar = true
    switch (type) {
      case 'success':
        options.color = 'success'
        break
      case 'error':
        options.color = 'error'
        break
      case 'warning':
        options.color = 'warning'
        break
      case 'loading':
        options.color = 'loading'
        break

      default:
        break
    }
    store.commit('setSnackbar', options)
  },
  success (text) {
    this.open('success', { 'text': text })
  },
  error (text) {
    this.open('error', { 'text': text })
  },
  info (text) {
    this.open('info', { 'text': text })
  },
  warning (text) {
    this.open('warning', { 'text': text })
  },
  loading (text) {
    this.open('loading', { 'text': text })
  }
}

export default Message
