import Hotel from '../views/Hotel.vue'

export default [
  {
    path: '/',
    name: 'hotel',
    component: Hotel,
    children: [
      {
        path: '/login',
        name: '登录',
        // route level code-splitting
        //   // this generates a separate chunk (about.[hash].js) for this route
        //   // which is lazy-loaded when the route is visited.
        component: () =>
          import(/* webpackChunkName: "login" */ '../views/Login.vue')
        // component: Login
      },
      {
        path: 'roomState',
        name: '房态',
        meta: { auth: true },
        component: () =>
          import(/* webpackChunkName: "roomState" */ '../views/RoomState.vue')
      },
      {
        path: 'order',
        name: '订单',
        meta: { auth: true },
        component: () =>
          import(/* webpackChunkName: "order" */ '../views/Order.vue')
      },
      {
        path: 'guest',
        name: '客人',
        meta: { auth: true },
        component: () =>
          import(/* webpackChunkName: "guest" */ '../views/Guest.vue')
      },
      {
        path: 'report',
        name: '报表',
        meta: { auth: true },
        component: () =>
          import(/* webpackChunkName: "report" */ '../views/Report.vue'),
        children: [
          {
            path: 'overview',
            name: '营业概览',
            meta: { auth: true },
            component: () =>
              import(/* webpackChunkName: "overview" */ '../views/report/Overview.vue')
          }
        ]
      },
      {
        path: 'setting',
        name: '设置',
        meta: { auth: true },
        component: () =>
          import(/* webpackChunkName: "setting" */ '../views/Setting.vue')
      }
    ]
  },
  {
    path: '*',
    name: '404',
    component: () =>
    import('../views/NotFound.vue')
  }
]
