const sysConfig = {
  state: {
    currTitle: '来客酒店云管家'
  },
  // getters 相当于计算属性
  getters: {},
  // 更改状态 同步
  mutations: {
    setCurrTitle (state, currTitle) {
      state.currTitle = currTitle || state.currTitle
    }
  },
  // 可以异步
  actions: {
  }
}

export default sysConfig
