import axios from './axiosConfig'
const usrP = '/api'

export const login = json => {
  return axios.post(usrP + '/login', json)
}

export const getCurrUser = () => {
  return axios.get(usrP + '/currUser')
}

export const logout = () => {
  return axios.get(usrP + '/logout')
}

export const changePwd = json => {
  return axios.post(usrP + '/user/pwd', json)
}

export const register = json => {
  return axios.post(usrP + '/register', json)
}

export const addUser = user => {
  return axios.post(usrP + '/user', user)
}

export const getUsers = () => {
  return axios.get(usrP + '/user')
}
