const message = {
  state: {
    snackbar: false,
    text: '消息提示',
    color: 'info',
    timeout: 2500
  },
  // getters 相当于计算属性
  getters: {},
  // 更改状态 同步
  mutations: {
    setSnackbar (state, snackbar) {
      state.snackbar = snackbar.snackbar
      state.text = snackbar.text ? snackbar.text : state.text
      state.color = snackbar.color ? snackbar.color : state.color
      state.timeout = snackbar.timeout ? snackbar.timeout : state.timeout
    }
  },
  // 可以异步
  actions: {
    msg (context, msg) {
      context.commit('setSnackbar', true)
    }
  }
}

export default message
