import { getDicByType } from '@/api/dictionary.js'

const dictionary = {
  state: {
    dictionaries: null,
    roomState: null,
    channel: null,
    pay_type: null,
    pay_method: null,
    consume_item: null,
    income_class: null,
    order_ctate_code: null
  },
  // getters 相当于计算属性
  getters: {
    getText: (state) => (type, val) => {
      return val && state[type] ? state[type].filter(i => val === i.dicCommonCode)[0].dicCommonName : ''
    }
  },
  // 更改状态 同步
  mutations: {
    setDictionaries (state, setDictionaries) {
      state.setDictionaries = setDictionaries
    },
    roomState (state, data) {
      state.roomState = data
    },
    channel (state, channel) {
      state.channel = channel
    },
    pay_type (state, payType) {
      state.pay_type = payType
    },
    pay_method (state, data) {
      state.pay_method = data
    },
    consume_item (state, data) {
      state.consume_item = data
    },
    income_class (state, data) {
      state.income_class = data
    },
    order_ctate_code (state, data) {
      state.order_ctate_code = data
    }
  },
  // 可以异步
  actions: {
    getDicByType (context, type) {
      getDicByType(type).then(res => {
        if (res.data.code === 0) {
          let data = res.data.data
          context.commit(type, data)
        } else {
          console.log(res.data.code + '：' + res.data.msg)
        }
      })
    }
  }
}

export default dictionary
