import { getRoomState } from '@/api/room.js'

const room = {
  state: {
    rooms: null
  },
  // getters 相当于计算属性
  getters: {
    roomNum: state => {
      let roomNum = {}
      roomNum.total = state.rooms ? state.rooms.length : 0
      roomNum.vacant = 0
      roomNum.rented = 0
      roomNum.dirty = 0
      roomNum.repair = 0
      roomNum.occupancy = 0
      if (state.rooms) {
        state.rooms.forEach(room => {
          if (room.roomState === 'vacant') {
            roomNum.vacant++
          }
          if (room.roomState === 'rented' || room.roomState === 'rented_hour') {
            roomNum.rented++
          }
          if (room.roomState === 'dirty') {
            roomNum.dirty++
          }
          if (room.roomState === 'repair') {
            roomNum.repair++
          }
        })
        roomNum.occupancy = (Math.round(roomNum.rented / roomNum.total * 10000) / 100.00 + '%')
      }
      return roomNum
    }
  },
  // 更改状态 同步
  mutations: {
    setRooms (state, rooms) {
      state.rooms = rooms
    }
  },
  // 可以异步
  actions: {
    getRooms (context) {
      getRoomState().then(res => {
        if (res.data.code === 0) {
          let roomStates = res.data.data
          context.commit('setRooms', roomStates)
        } else {
          console.log(res.data.code + '：' + res.data.msg)
        }
      })
    }
  }
}

export default room
