<template>
  <v-app v-touch="{
      left: () => swipe('Left'),
      right: () => swipe('Right'),
      up: () => swipe('Up'),
      down: () => swipe('Down')
    }">
    <v-navigation-drawer
      app
      v-model="drawer"
      width="150"
      temporary
      :disable-resize-watcher="false"
      touchless
    >
      <v-toolbar
        flat
        class="transparent"
      >
        <v-list class="pa-0">
          <v-list-tile>
            <v-list-tile-content>
              <v-list-tile-title>
                <div>{{ currHotel.hotelName }}</div>

              </v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
      </v-toolbar>
      <v-list>
        <v-list-tile
          v-for="item in drawerItems"
          :key="item.title"
          @click=""
          :to="item.to"
        >
          <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>

          <v-list-tile-content>
            <v-list-tile-title>{{ item.title }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
      <center><a href="/">访问电脑版</a></center>

    </v-navigation-drawer>

    <v-toolbar app>
      <v-toolbar-side-icon
        class="hidden-sm-and-up"
        @click="drawer=!drawer"
      >
      </v-toolbar-side-icon>
      <v-toolbar-title>
        <div class="hidden-sm-and-up">{{ currTitle }}</div>
        <span class="hidden-sm-and-down">来客酒店云管家 &nbsp;&nbsp;&nbsp;</span>
        <span class="hidden-xs-only">{{ currHotel.hotelName }}</span>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-xs-only">
        <v-btn
          v-for="(item, index) in drawerItems"
          :key="index"
          :to="item.to"
          flat
        >{{ item.title }}</v-btn>
      </v-toolbar-items>
      <v-menu offset-y>
        <template v-slot:activator="{ on }">
          <v-avatar
            color="red"
            size="32"
            v-on="on"
            v-ripple
          >
            <span class="white--text headline">{{headText}}</span>
          </v-avatar>
        </template>
        <v-list>
          <v-list-tile
            @click.stop=""
            v-ripple
          >
            <v-list-tile-title>修改密码</v-list-tile-title>
          </v-list-tile>
          <v-list-tile
            @click.stop="collect.cashType = 'out'; collect.show = true"
            v-ripple
          >
            <v-list-tile-title>财务收账</v-list-tile-title>
          </v-list-tile>
          <v-list-tile
            @click.stop="collect.cashType = 'in'; collect.show = true"
            v-ripple
          >
            <v-list-tile-title>添加备用金</v-list-tile-title>
          </v-list-tile>
          <v-list-tile
            @click="logout"
            v-ripple
          >
            <v-list-tile-title>退出系统</v-list-tile-title>
          </v-list-tile>
        </v-list>
      </v-menu>
    </v-toolbar>
    <v-content>
      <router-view></router-view>
    </v-content>
    <!-- <v-footer app>ss</v-footer> -->
    <Collect
      :show="collect.show"
      :cash-type="collect.cashType"
      @collect-close="collect.show = false"
    ></Collect>
    <v-btn
      v-show="showTop"
      color="primary"
      dark
      fab
      fixed
      bottom
      right
      @click="$vuetify.goTo(0)"
    >
      <!-- <v-icon>{{ activeFab.icon }}</v-icon>
        <v-icon>close</v-icon> -->
      返回顶部
    </v-btn>
  </v-app>
</template>

<script>
import { mapState, mapActions } from "vuex";
import Collect from "../components/personal/Collect.vue";
import { setInterval, setTimeout } from "timers";
export default {
  name: "",
  components: {
    Collect
  },
  data() {
    return {
      drawer: false,
      drawerItems: [
        { title: "房态", icon: "home", to: "/roomState" },
        { title: "订单", icon: "event", to: "/order" },
        { title: "客人", icon: "assignment_ind", to: "/guest" },
        { title: "报表", icon: "table_chart", to: "/report/overview" },
        { title: "设置", icon: "settings", to: "/setting" }
      ],
      collect: {
        show: false,
        cashType: "out"
      },
      menu: false,
      // 返回顶部相关
      scrollTop: 0,
    };
  },
  computed: {
    headText() {
      return this.currUser && this.currUser.realName
        ? this.currUser.realName.charAt(0)
        : "囧";
    },
    breakpoint() {
      return this.$vuetify.breakpoint.name;
    },
    showTop() {
      return this.scrollTop > 200 ? true : false;
    },
    ...mapState({
      isLogin: state => state.user.isLogin,
      isFirst: state => state.user.isFirst,
      currUser: state => state.user.currUser,
      currHotel: state => state.user.currHotel,
      currTitle: state => state.sysConfig.currTitle
    })
  },
  watch: {
    breakpoint(breakpoint) {
      if (breakpoint != "xs") {
        this.drawer = false;
      }
    },
  },
  mounted() {
    if (this.$route.path === "/") {
      this.$router.push("report");
    }
    this.getRooms();
    this.getDicByType("pay_method");

    // 监听 scrollTop
    window.addEventListener("scroll", this.getScrpllTop);
  },
  methods: {
    swipe(direction) {
      console.log(1);
      console.log(this.$vuetify);
      if (direction == "Right") {
        this.drawer = true;
      }
    },
    snackbar() {
      console.log("sads");
    },
    getScrpllTop() {
      this.scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
    },
    ...mapActions({
      getRooms: "getRooms",
      getDicByType: "getDicByType",
      logout: "logout"
    })
  }
};
</script>

<style>
</style>
